/* src/components/player.css */
.player {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px;
  }
  
  .disc {
    width: 120px;
    height: 120px;
    position: relative;
  }
  
  .disc-img {
    width: 100%;
    height: 100%;
    transition: transform 1s linear;
  }
  
  .disc.rotate .disc-img {
    transform: rotate(360deg);
  }
  
  .progress-bar {
    width: 100%;
    margin-top: 10px;
  }
  
  progress {
    width: 100%;
    height: 10px;
    appearance: none;
  }
  
  progress::-webkit-progress-bar {
    background-color: #f0f0f0;
  }
  
  progress::-webkit-progress-value {
    background-color: #4caf50;
  }
  
  progress::-moz-progress-bar {
    background-color: #4caf50;
  }
  
  .play-pause-button {
    background-color: #4caf50;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .play-pause-button:hover {
    background-color: #45a049;
  }
  
  .player-status {
    margin-top: 10px;
    color: red;
  }
  